import request from '@/utils/request'

export default {
  // 用户登录
  login(member) {
    return request({
      url: `/ucenter/member/login`,
      method: 'post',
      data: member
    })
  },
  // 获取用户信息
  getMemberInfo() {
    return request({
      url: `/ucenter/member/getMemberInfo`,
      method: 'get'
    })
  }
}